<template>
  <article class="contactUs subPage">
    <header>
      <h2>聯絡我們</h2>
      <p>Contact Us</p>
    </header>
    <section class="wrapper style5">
      <div class="inner">
        <img src="@/assets/images/contact-us.jpg" alt="contact us">
        <h4><i class="icon fa-envelope" /> 聯絡我們</h4>
        <ValidationObserver ref="observer" tag="form" @submit.prevent="submitHandler" >
          <div class="row gtr-uniform">
            <div class="col-6 col-12-xsmall">
              <ValidationProvider rules="required" v-slot="{ errors }">
                <input v-model="formData.name" type="text" name="name" id="name" placeholder="姓名" />
                <small class="text-danger">{{ errors[0] }}</small>
              </ValidationProvider>
            </div>
            <div class="col-6 col-12-xsmall">
              <ValidationProvider rules="required|email" v-slot="{ errors }">
                <input v-model="formData.email" type="email" name="email" id="email" placeholder="電子郵件" />
                <small class="text-danger">{{ errors[0] }}</small>
              </ValidationProvider>
            </div>
            <div class="col-12">
              <textarea v-model="formData.message" name="message" id="message" placeholder="問題" rows="5"></textarea>
            </div>
            <div class="col-6">
              <VerifyModules ref="verifyModules"
                :verifyCode="verifyCode"
                @updateVerifyCode="updateVerifyCode"/>
            </div>
            <div class="col-12">
              <ul class="actions">
                <li><input @click="submitHandler" type="submit" value="送出" class="primary" /></li>
                <li><input @click="restHandler" type="reset" value="清除" /></li>
              </ul>
            </div>
          </div>
        </ValidationObserver>
      </div>
    </section>
  </article>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend, localize } from 'vee-validate'
import { required, email } from 'vee-validate/dist/rules'
import TW from 'vee-validate/dist/locale/zh_TW.json'
import VerifyModules from './verifyModules'
localize('zh_TW', TW)
extend('email', email)
extend('required', {
  ...required,
  message: '必填欄位！'
})

export default {
  name: 'ContactUs',
  components: {
    VerifyModules,
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      formData: {
        name: '',
        email: '',
        message: ''
      },
      verifyCode: null
    }
  },
  methods: {
    // 清除
    restHandler () {
      this.verifyCode = null
      this.$refs.observer.reset()
      this.$refs.verifyModules.$refs.Verify.refresh()
      this.formData = this.$options.data.call(this).formData
    },
    // 檢查驗證碼
    async submitHandler () {
      this.$refs.verifyModules.$refs.Verify.checkCode()
      const isValid = await this.$refs.observer.validate()
      if (isValid && this.verifyCode) {
        alert('成功送出！')
        this.restHandler()
      } else return false
      // this.$refs.observer.validate(evt => {
      //   console.log(evt)
      // })
      // if (this.errors.any()) {
      //   alert('submit')
      // }
    },
    // 驗證碼更新 Code
    updateVerifyCode (val) {
      this.verifyCode = val
    }
  }
}
</script>

<style lang="scss">
.contactUs {
  .wrapper.style5 .inner img {
    margin-bottom: .8rem;
    border-radius: 3px;
  }
}
</style>
